<template>
    <FyLayout id="Data" bgColor="transparent">
        <Block  align="center">
            <Page @on-change="pageChange" :current="params.pageNo+1" :page-size="params.size" :total="count" simple></Page>
        </Block>
    </FyLayout>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "Data",
  data() {
    return {
      text: "",
      defultSrc,
      list: [],
      count: 0,
      params: {
        pageNo: 0,
        size: 20,
        title: ""
      }
    };
  },
  computed: mapState({
    userId: state => (state.users ? state.users.users.id : "")
  }),
  created() {
    this.getList();
  },
  methods: {
    pageChange(page) {
      this.params.pageNo = page - 1;
    },
    async getList() {
    //   const { data } = await this.$http({
    //     url: this.$Inter.ART_BY_USER(this.userId),
    //     params: this.params
    //   });
    //   const { rows = [], pageNo, size, count } = data;
    //   this.list = rows;
    //   this.params.pageNo = pageNo;
    //   this.params.size = size;
    //   this.count = count;
    //   this.$Message.success("加载成功！");
    }
  }
};
</script>

<style lang="less">
@import "./style.less";
</style>



